export default {
	props:{
		items:{
			type: Array
		},
		name:{
			type: String,
			default: '测试'
		},
		star:{
			type: String,
			default: 'false'
		}
	},
	data() {
		return {
			index: -1
		};
	},
	methods:{
		itemChange:function(e){
			let index = e.detail.value
			this.index = index
			this.$emit("mychange",this.items[index].id)
		}
	}
}
